
import XHeader from 'vux/src/components/x-header/index.vue'

import dayuan from "../../common/js/dayuan.js";
import dayjs from "dayjs";

import Comments from "./Comments/Comments";
import { mapState } from "vuex";
export default {
data() {
return {
task: {},
msg: {}
};
},
components: {
XHeader,

Comments
},
computed: {
...mapState(["CurrentUserID"]),
exeStr() {
if (this.task.Executors) {
let arr = this.task.Executors.map(item => item.Name);
return arr;
}
},
exeStr2() {
if (this.task.Senders) {
let arr = this.task.Senders.map(item => item.Name);
return arr;
}
},
workState() {
let str = this.task.TaskType === 1 ? "客户跟进任务" : "普通任务";
return str;
},
taskPriority() {
return dayuan.Proority(this.task["TaskPriority"]);
},
createTime() {
if (this.task.BeginDate) {
let time = dayjs(this.task.BeginDate).format("YYYY-MM-DD HH:mm");
return time;
}
},
exeImg() {
let arr = [];
this.task.Executors.forEach((item, key) => {
arr.push(dayuan.aImg(item.UserID));
});
return arr;
},
exeImg2() {
let arr = [];
this.task.Senders.forEach((item, key) => {
arr.push(dayuan.aImg(item.UserID));
});
return arr;
},
addComParam() {
return {
ID: 0,
CommentType: 1,
DataID: this.$route.query.id,
Content: "",
ReplyID: 0
};
}
},
methods: {
back() {
if (this.$route.query.backIndex && this.$route.query.backIndex == 1) {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
} else {
this.$router.back();
}
},
getWorkInfo(id) {
return this.Axios.get(`/CrmTask/GetCrmTaskInfoByID/${id}`)
.then(res => {
this.task = res.data.Data;
let userID = this.task.Executors.map(v => v.UserID);
userID.forEach((val, index) => {
if (val === this.CurrentUserID) {
userID.splice(index, 1);
}
});
this.msg = {
SendUsers: userID,
EvName: "crm-task",
ImgPath: `${down}/mdtcrm/CRMTask.jpg`,
Title: `您有一条新的任务评论消息`,
Content: `${this.task.TaskContent}`,
Params: [this.task.ID]
};


})
.catch(err => {
console.log(err);
});
},
scrollTow() {
this.$refs["scroll"].scrollTo(0, -160, 600);
}
},
mounted() {
setTimeout(() => {
let hei = document.body.clientHeight;
this.$refs["client-list"].style.height = `${hei - 48}px`;
this.getWorkInfo(this.$route.query.id);
}, 0);
},
beforeRouteEnter(to, from, next) {
if (from.path == "/") {
if (window.plus) {
window.setTimeout(() => {
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
});
}, 0);
} else {
document.addEventListener("plusready", () => {
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
});
});
}
}
next();
}







};



